"use server";

import { cookies } from "next/headers";
import jwt from "jsonwebtoken";
import { prisma } from "@/lib/prisma";

export async function getUser() {
  try {
    const cookieStore = await cookies();
    const token = cookieStore.get("token")?.value;
    if (!token) {
      return null;
    }
    const decoded = jwt.verify(token, process.env.JWT_SECRET!) as {
      userId: string;
    };
    return decoded;
  } catch (error) {
    console.error("Error in getUser:", error);
    return null;
  }
}

export async function getUserName() {
  try {
    const user = await getUser();
    if (!user) {
      return null;
    }
    const userData = await prisma.user_data.findUnique({
      where: {
        user_id: user.userId,
        user_state: "0", // Ï†ïÏÉÅ ÌöåÏõêÎßå Ï°∞Ìöå
      },
      select: { user_name: true },
    });
    return userData?.user_name || null;
  } catch (error) {
    console.error("Error fetching user name:", error);
    return null;
  }
}

export async function getUserId() {
  try {
    const user = await getUser();
    if (!user) {
      return null;
    }
    const userData = await prisma.user_data.findUnique({
      where: { user_id: user.userId },
      select: { id: true },
    });
    return userData?.id || null;
  } catch (error) {
    console.error("Error fetching user name:", error);
    return null;
  }
}

export async function deleteUser() {
  try {
    const user = await getUser();
    if (!user) {
      return { success: false, error: "Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§." };
    }

    // user_stateÎ•º 1Î°ú Î≥ÄÍ≤ΩÌïòÏó¨ ÌöåÏõê ÌÉàÌá¥ Ï≤òÎ¶¨
    await prisma.user_data.update({
      where: { user_id: user.userId },
      data: { user_state: "1" },
    });

    // Ïø†ÌÇ§ÏóêÏÑú ÌÜ†ÌÅ∞ Ï†úÍ±∞
    const cookieStore = await cookies();
    cookieStore.delete("token");

    return { success: true };
  } catch (error) {
    console.error("ÌöåÏõê ÌÉàÌá¥ Ïò§Î•ò:", error);
    return { success: false, error: "ÌöåÏõê ÌÉàÌá¥ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§." };
  }
}
